import { useState } from "react";
import Head from "next/head";
import { NavBar /*  Article, Footer */ } from "../components";
import { findWord, Centered, Articles } from "./index";

export default () => {
  const [value, setValue] = useState("");
  const articleFilter = ({ headline, author, topics, body }) =>
    (topics && topics.some(topic => topic.toLowerCase().startsWith(value))) ||
    (author && findWord(value, author.toLowerCase())) ||
    (headline && findWord(value, headline.toLowerCase())) ||
    (body && findWord(value, body.toLowerCase()));
  return (
    <Centered>
      <Head>
        <title>WebAssembly</title>
      </Head>
      <NavBar
        onChange={value => setValue(value.toLowerCase())}
        value={value}
        experimental
      />
    </Centered>
  );
};
